import React, {Component} from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography';
import LinearProgress from '@material-ui/core/LinearProgress';

import Category from './CategoryCard'
import axios from 'axios';

const styles = theme => ({
    layout: {
        width: 'auto',
        marginLeft: theme.spacing.unit * 3,
        marginRight: theme.spacing.unit * 3,
        [theme.breakpoints.up(1100 + theme.spacing.unit * 3 * 2)]: {
          width: 1100,
          marginLeft: 'auto',
          marginRight: 'auto',
        },
      },
});

class Categories extends Component {
    state = {
        loading: true,
        categories:[],
    }

    componentDidMount() {
        axios.get('https://api.avgle.com/v1/categories')
        .then(res => {
          this.setState({
              loading:false,
            categories: res.data.response.categories,
          });
          console.log(res.data)
        })
    }

    render(){
        const { classes } = this.props;
        if(this.state.loading){
            return(
                <LinearProgress className={classes.progress} color="secondary" />
            )
        }

        return(
            <div className={classes.layout}>
                {this.state.categories ? (
                    <div>
                    <Typography variant="h6" color="inherit" noWrap>Categories</Typography>
                    <Grid container spacing={0} style={{padding:0}} alignItems="center" justify="center">
                        {this.state.categories.map(category =>(
                            <Grid item key={category.CHID} sm={6} md={4} lg={3} style={{padding:10}} >
                                <Category data={category}></Category>
                            </Grid>
                        ))}
                    </Grid>
                    </div>

                ): "No category found"}
            </div>
        )
    }
}

Categories.propTypes = {
    classes: PropTypes.object.isRequired,
  };

export default withStyles(styles) (Categories);