import React, {Component} from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography';
import LinearProgress from '@material-ui/core/LinearProgress';

import Collection from './CollectionCard'
import axios from 'axios';

const styles = theme => ({
    layout: {
        width: 'auto',
        marginLeft: theme.spacing.unit * 3,
        marginRight: theme.spacing.unit * 3,
        [theme.breakpoints.up(1100 + theme.spacing.unit * 3 * 2)]: {
          width: 1100,
          marginLeft: 'auto',
          marginRight: 'auto',
        },
      },
});

class Collections extends Component {
    state = {
        loading: true,
        collections:[],
    }

    componentDidMount() {
        console.log("きてる")

        axios.get('https://api.avgle.com/v1/collections/0?limit=50')
        .then(res => {
            console.log(res.data.response.collection)
          this.setState({
              loading:false,
              collections: res.data.response.collections
          });
        })
        // axios.get('http://127.0.0.1:8080/collection/')
        // .then(res => {
        //   this.setState({
        //       loading:false,
        //       collections: res.data
        //   });
        //   console.log(res.data)
        // })
    }

    render(){
        const { classes } = this.props;
        if(this.state.loading){
            return(
                <LinearProgress className={classes.progress} color="secondary" />               
            )
        }

        return(
            <div className={classes.layout}>
                {this.state.collections ? (
                    <div>
                    <Typography variant="h6" color="inherit" noWrap>Collections</Typography>
                    <Grid container spacing={0} style={{padding:0}} alignItems="center" justify="center">
                        {this.state.collections.map(collection =>(
                            <Grid item key={collection.id} sm={6} md={4} lg={3} style={{padding:10}} >
                                <Collection data={collection}></Collection>
                            </Grid>
                        ))}
                    </Grid>
                    </div>

                ): "No collection found"}
            </div>
        )
    }
}

Collections.propTypes = {
    classes: PropTypes.object.isRequired,
  };

export default withStyles(styles) (Collections);